/* Container for the entire Home page */
.Home-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  gap: 20px;
  max-width: 1400px;
  margin: 0 auto;
}

/* Top league section positioned on the left */
.top-league {
  width: 200px;
  background-color: #1c1c1c;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  padding: 20px;
  color: #fff;
  font-family: Arial, sans-serif;
  flex-shrink: 0;
  margin-left: 15px;
}

/* Table styling for league list */
.top-league table {
  width: 100%;
  border-collapse: collapse;
}

.top-league td {
  padding: 12px;
  font-size: 16px;
  color: #fff;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.top-league td:hover {
  background-color: #333333;
  border-radius: 5px;
}

/* Matches section centered */
.home-matches {
  flex-grow: 1;
  max-width: 800px;
  padding: 20px;
  background-color: #2a2a2a;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  margin: 0 auto;
}

/* League standings positioned on the right */
.home-leagues {
  width: 300px;
  border-radius: 10px;
  padding: 20px;
  color: #fff;
  font-family: Arial, sans-serif;
  flex-shrink: 0;
  max-height: 90vh;
  overflow-y: auto;
}

/* League standings table */
.home-leagues .standings-table {
  width: 100%;
  border-collapse: collapse;
}

.home-leagues .standings-table th,
.home-leagues .standings-table td {
  padding: 10px;
  font-size: 14px;
  color: #fff;
}

.home-leagues .standings-table th {
  background-color: #2c2c2c;
}

.home-leagues .standings-table tr:nth-child(even) {
  background-color: #2a2a2a;
}

.home-leagues .standings-table tr:nth-child(odd) {
  background-color: #252525;
}
